@import '../global.scss';
.navbar{
    padding: 10px 100px;
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-between;
    
   
    @include mobile{
        padding: 10px 20px;
    }
    
    .link{
        text-decoration: none;
        color: #000;
        text-transform: uppercase;
    }

    .item{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;

        
    }

    .left{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        @include mobile{
            display: none;
        }
    }
    .center{
       font-size: 30px;
       cursor: pointer;
       @include mobile{
       font-size: 20px;
    }
    }
    .right{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        @include mobile{
            display: none;
        }

        .icons{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 8px;

            .cart{
                position: relative;
              

                span{
                    position: absolute;
                    top: -10px;
                    right: -10px;
                    background: green;
                    color: #fff;
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    font-size: 15px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    
                   
                }
            }
        }
    }
    .menuIcon{
        display: none;
        @include mobile{
            display: block;
        }
    }
}