.menu{
    position: absolute;
    top: 0;left: 0;
    right: 0;margin-top: 60px;
    z-index: 99;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
    background-color: rgba(20, 127, 20,.2);
    padding: 10px;
    .item{
        
        .link{
            color: aliceblue;
            font-size: 20px;
            padding: 10px;
        }
    }
    .icons{
        display: flex;
        gap: 50px;
        color: aliceblue;
        .cart{
            position: relative;
          

            span{
                position: absolute;
                top: -10px;
                right: -10px;
                background: green;
                color: #fff;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                font-size: 15px;
                display: flex;
                align-items: center;
                justify-content: center;
                
               
            }
        }
    }
}